.fourth-screen {
  position: relative;

  &__container::before {
      content: '';
      position: absolute;
      right:1000px;
      bottom:250px;
      background: #0041C4;
      height: 555px;
      width: 50vw;
      margin-top: 199px;
    
  }

  &__box {
    display: flex;
    align-items: center;
    height: 555px;
    background: #0041C4;
    border-bottom-right-radius:200px ;
    margin-bottom: 250px;

  
    
  }

  &__head {
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 64px;
    max-width: 660px;
    margin-bottom: 100px;

  }

  &__image {
    display: flex;
    position: relative;
    max-height: 592px;
    max-width: 290px;
    margin-right: 90px;
    margin-bottom: -200px;

  }


  &__slide {
    left:16px;
    top:16px;
    position: absolute;
    height: 559px;
    width: 258px;
  }

  &__listNav {
    display: flex;
    flex-direction: row;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    color: #FFFFFF;

    margin-bottom: 32px;
   

  }

   &__text {
      max-width:450px;
      color: #FFFFFF;
      margin-bottom:41px ;
    }

  &__itemNav {

    &:first-child {
      opacity: 1.0;
      text-decoration-line: underline;
    }

    &:not(:first-child) {
      opacity: 0.5;
    }

    &:not(:last-child) {
      margin-right: 35px;
    }

    
  }

  &__buttonL {
    background:rgba(255, 255, 255, 0.390488);
    width: 50px;
    height: 50px;
    padding:16px 17px 16px 15px;
    border-radius: 70%;
    margin-right: 12px;


    &:last-child {
      transform: rotate(180deg);
    }

    &:hover {
      background:#F8C984;
      box-shadow: 0px 2px 34px rgba(0, 0, 0, 0.205248);
    }

    
  }
}