.fifth-screen {

  &__imageL {
    position: relative;
    max-width: 456px;
    // margin-right: 100px;
    margin-left:-250px;
    z-index: 1;
  }

  &__imageR {
    max-width: 612px;
    // margin-right: -300px;
  }

  &__content {
    display: flex;
    align-items: center;
    margin-top:-370px;
    margin-bottom: 100px;
  }

  &__info {
    width:580px ;
