.second-screen {
  

  &__row {
    display: flex;
    flex-direction: row;
    margin-bottom: 140px;
  }

  &__img {
    display:inherit;
    height: 586px;
    margin-left:-120px;
    margin-right:120px;
  }

  // &__img img{

  // }

  &.bg {
    width:750px;

  }

  &.phone {
    margin-left:-380px;
    z-index: 1;
  }

  &.screen {
    margin-left: -380px;
    margin-top:14px;
    max-height:559px ;
    z-index: 2;

  }


  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__info {
    
    max-width: 551px;
    font-size: 34px;
    line-height: 48px;
    letter-spacing: 0.25px;

  }

  &__list {
    display: inherit;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-bottom: 160px;
  }

  &__item {
    text-align: center;
    max-width: 264px;


    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__title {
    margin-bottom:10px;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.25px;
    color: #252849;
  }

  &__text {

  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.1px;
  mix-blend-mode: normal;
  opacity: 0.7;
  }

  &__fon {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    margin:0 auto 24px;
    background: #0041C4; 
    width: 104px;
    height: 104px;
    border-radius: 28px;

  
  }

  // &__icon {
  //   // margin:auto 0;
  //   // height: 70%;
  //   // width: 70%;
  // }
}