.sixth-screen {

  &__container {
    background:#0041C4; 
    padding: 150px  52px 70px 52px;
    border-bottom-right-radius:200px ;
    border-top-left-radius:200px ;
    align-items: center;

  }

  &__head {
    font-style: normal;
    font-weight: bold;
    font-size: 34px;
    line-height: 48px;
    text-align: center;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    max-width: 700px;
    margin-bottom: 30px;
  }

  &__info {
    max-width:600px ;
    text-align: center;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #FFFFFF;
    margin-bottom:52px ;
  }

  &__icon {
    display: flex;
  }

  &__apple {
    margin-right: 24px;
  }
}